import React, { Component } from 'react';
import mockData from './mock.json';
import { LineChart, Line, XAxis, YAxis, Label, ResponsiveContainer } from 'recharts';
import CssBaseline from '@material-ui/core/CssBaseline';
import './table.css';


import { Typography, Button } from '@material-ui/core';




const COLUMNS = [
    'Nimbo ID',
    'Company',
    'Local Time',
    'Public Wifi',
    'Robox',
    'Nimbo App',
    'Slam',
    'Software Version',
    'Hardware Version',
    'Mode',
    'Live Streaming',
    'Total Patrol Time',
    'Log',
];


const COLUMN_MAP = new Map();

for (let i = 0; i < COLUMNS.length; i++) {
    COLUMN_MAP.set(COLUMNS[i], i);
};

/** 定义一个React Component class. */
class Table extends Component {
    constructor() {
        super(); /** 实现继承parent component的所有功能. */
        this.mockData = mockData;
    }

    // Question:  这是一个function 吗？
    TableColumns() {

        const displayColumnHeaders = [];
        for (let i = 0; i < COLUMNS.length; i++) {
            displayColumnHeaders.push(<th >{COLUMNS[i]}</th>);
        }
        return (
            <tr>

                {/* Question：老师这里真的没有看懂。。。 */}
                {/* 这里的的COLUMNS不是只有一列元素吗？ colum和idx分别代表什么？？？？？？？？？？？？？？ */}
                {/* {COLUMNS.map((elem, index) => {return <th key={index}>{elem}</th>;})} */}

                {displayColumnHeaders}
            </tr>
        );
    }


    TableDataRow(rows, rowName) {
        return (
            <tr key={rowName}>
                {rows.map((row, idx) => {
                    return <td key={idx}>{row}</td>;
                })}
            </tr>
        );
    }

    /** 生成HTML component. render() 是React library 用来生成并输出HTML组件的method. */
    render() {
        // const tableDataRow1 = COLUMNS.map(columnName => this.mockData[0][columnName]);
        // const tableDataRow2 = COLUMNS.map(columnName => this.mockData[1][columnName]);
        // const TableDataRows = this.mockData.map((mockDataRow, idx) => {
        //     return this.TableDataRow(COLUMNS.map(columnName => mockDataRow[columnName]), `row${idx}`);
        // })


        const TableDataRows = [];

        for (let i = 0; i < this.mockData.length; i++) {
            const mockDataRow = this.mockData[i];

            // const rowColumnData = [];
            // /** Get the display order. */
            // for(let j = 0; j < COLUMNS.length; j++ ) {
            //     const columnName = COLUMNS[i];
            //     rowColumnData.push(
            //         <td key={j}>{mockDataRow[columnName]}</td>
            //     )
            // }


            // TableDataRows.push((mockDataRow, idx) => {
            //     return this.TableDataRow(COLUMNS.map(columnName => mockDataRow[columnName]), `row${idx}`);
            // })
            const rowData = COLUMNS.map(columnName => mockDataRow[columnName]);

            const currentDataRow = (
                <tr key={`row${i}`}>
                    {rowData.map((row, idx) => {
                        switch (idx) {
                            case COLUMN_MAP.get('Nimbo ID'):
                                return (
                                    <td>
                                        <Button className="NimboIDButton" >
                                            <text style={{ color: 'black', fontSize: 12, fontWeight: 400}}>
                                                {row}
                                            </text>
                                        </Button>
                                    </td>
                                );
                            case COLUMN_MAP.get('Company'):
                                return (
                                    <td>
                                        <Button>
                                        <text style={{ color: 'black', fontSize: 12, fontWeight: 400}}>
                                                {row}
                                            </text>
                                        </Button>
                                    </td>
                                );
                            case COLUMN_MAP.get('Robox'):
                                if (row.includes('Online')) {
                                    return (
                                        <td>
                                            <span style={{ color: 'green' }}>{row}</span>
                                        </td>
                                    );
                                } else {
                                    return (
                                        <td>
                                            <span style={{ color: 'red' }}>{row}</span>
                                        </td>
                                    );
                                }
                            case COLUMN_MAP.get('Nimbo App'):
                                if (row.includes('Online')) {
                                    return (
                                        <td>
                                            <span style={{ color: 'green' }}>{row}</span>
                                        </td>
                                    );
                                } else {
                                    return (
                                        <td>
                                            <span style={{ color: 'red' }}>{row}</span>
                                        </td>
                                    );
                                }
                            case COLUMN_MAP.get('Slam'):
                                if (row.includes('Online')) {
                                    return (
                                        <td>
                                            <span style={{ color: 'green' }}>{row}</span>
                                        </td>
                                    );
                                } else {
                                    return (
                                        <td>
                                            <span style={{ color: 'red' }}>{row}</span>
                                        </td>
                                    );
                                }
                            case COLUMN_MAP.get('Software Version'):
                                if (row.includes('Updated')) {
                                    return (
                                        <td>
                                            <span style={{ color: 'green' }}>{row}</span>
                                        </td>
                                    );
                                } else {
                                    return (
                                        <td>
                                            <span style={{ color: 'red' }}>{row}</span>
                                        </td>
                                    );
                                }
                            case COLUMN_MAP.get('Hardware Version'):
                                if (row.includes('Updated')) {
                                    return (
                                        <td>
                                            <span style={{ color: 'green' }}>{row}</span>
                                        </td>
                                    );
                                } else {
                                    return (
                                        <td>
                                            <span style={{ color: 'red' }}>{row}</span>
                                        </td>
                                    );
                                }
                            default:
                                return <td key={idx}>{row}</td>;
                        }
                    })}
                </tr>
            );


            TableDataRows.push(currentDataRow)
        }

        return (
            <div className="table-component">
                <div class="table-wrapper">
                    <table>
                        <tbody>
                            {/** Intrapolation. */}
                            {this.TableColumns()}
                            {/* {this.TableDataRow(tableDataRow1, 'row1')}
                            {this.TableDataRow(tableDataRow2, 'row1')} */}
                            {TableDataRows}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}
export default Table;