import React, { Component } from 'react';
import './SideNav.css';
import { ListItem, ListItemIcon, ListItemText, Collapse, List, Divider, Typography } from '@material-ui/core';
import { ExpandLess, ExpandMore, MoveToInbox } from '@material-ui/icons';

class SideNav extends Component {
    constructor() {
        super();
        this.state = {
            isDevicesCollapseOpen: true,
            isLabelsCollapseOpen: true,
        };
    }

    /** Modifies the collapse component. */
    modifyDevicesCollapse() {
        this.setState({ isDevicesCollapseOpen: !this.state.isDevicesCollapseOpen });
    }
    modifyLabelsCollapse() {
        this.setState({ isLabelsCollapseOpen: !this.state.isLabelsCollapseOpen });
    }

    titleText(text) {
        return <Typography type="h3" style={{color: '#333'}}>{text}</Typography>
    }

    render() {
        return (
            // align 意思是在这个 div 里面的所有component额位置
            // question: 返回的是一个 <div> html component 对吗？？？？？？？？？？？？？
            <div className="sidenav-component" align="center" >
                <ListItem button onClick={() => this.modifyDevicesCollapse()}>
                    <ListItemIcon><MoveToInbox /></ListItemIcon>
                    <ListItemText primary={this.titleText('Devices')} />
                    {this.state.isDevicesCollapseOpen ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Divider />
                <Collapse in={this.state.isDevicesCollapseOpen} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                        <ListItem button className="sidenav-list-item">
                            Nimbo
                        </ListItem>
                        <ListItem button className="sidenav-list-item">
                            AiVR
                        </ListItem>
                    </List>
                </Collapse>


                <ListItem button onClick={() => this.modifyLabelsCollapse()}>
                    <ListItemIcon><MoveToInbox /></ListItemIcon>
                    <ListItemText primary={this.titleText('Labels')}/>
                    {this.state.isLabelsCollapseOpen ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Divider />

                <Collapse in={this.state.isLabelsCollapseOpen} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                        <ListItem button className="sidenav-list-item">
                            Zz's Label
                        </ListItem>
                        <ListItem button className="sidenav-list-item">
                            Old Wang's Label
                        </ListItem>
                        <ListItem button className="sidenav-list-item">
                            Stella's Label
                        </ListItem>
                    </List>
                </Collapse>
            </div>

        );
    }
}

// 一定要
export default SideNav;